<template>
  <div>
    <label for="">相关案例推荐</label>
    <div class="info">
      <ul id="myTab" class="nav nav-tabs">
        <li class="active">
          <a href="#A" data-toggle="tab">裁判文书</a>
        </li>
        <li><a href="#B" data-toggle="tab">典型案例</a></li>
        <li><a href="#C" data-toggle="tab">纠纷调解</a></li>
      </ul>

      <div id="myTabContent" class="tab-content">
        <div class="tab-pane fade in active" id="A">
          <table class="table  table-bordered  table-hover" style="width: 100%">
            <thead>
            <tr>
              <th style="text-align:center; color: #3a8dbe;">收藏</th>
              <th style="text-align:center; color: #3a8dbe;">登记时间</th>
              <th style="text-align:center; color: #3a8dbe;">案件名称</th>
              <th style="text-align:center; color: #3a8dbe;">简要情况</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in list1" style="border: none">
              <td style="width: 60px;text-align: center"><input type="checkbox" v-model="dissension_ms" :value="item"></td>
              <!--<td style="width: 100px">{{// new Date(item.registerDate).toLocaleDateString()}}</td>-->
              <td style="width: 100px">{{item.registerDate}}</td>
              <td style="width: 200px">{{item.disputeName}}</td>
              <td @click="caseDetails(item,'dissension_ms')" style="cursor: pointer" v-html="item.abstract">{{item.abstract}}</td>
            </tr>
            </tbody>
          </table>
        </div>

        <div class="tab-pane fade" id="B">
          <table class="table  table-bordered  table-hover" style="width: 100%">
            <thead>
            <tr>
              <th style="text-align:center; color: #3a8dbe;">收藏</th>
              <th style="text-align:center; color: #3a8dbe;">登记时间</th>
              <th style="text-align:center; color: #3a8dbe;">案件名称</th>
              <th style="text-align:center; color: #3a8dbe;">简要情况</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in list2" style="border: none">
              <td style="width: 60px;text-align: center"><input type="checkbox" v-model="dissension_dx" :value="item"></td>
              <td style="width: 100px">{{item.registerDate}}</td>
              <td style="width: 200px">{{item.disputeName}}</td>
              <td @click="caseDetails(item,'dissension_dx')" style="cursor: pointer"　v-html="item.abstract">{{item.abstract}}</td>
            </tr>
            </tbody>
          </table>
        </div>

        <div class="tab-pane fade" id="C">
          <table class="table  table-bordered  table-hover" style="width: 100%">
            <thead>
            <tr>
              <th style="text-align:center; color: #3a8dbe;">收藏</th>
              <th style="text-align:center; color: #3a8dbe;">登记时间</th>
              <th style="text-align:center; color: #3a8dbe;">案件名称</th>
              <th style="text-align:center; color: #3a8dbe;">简要情况</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in list3" style="border: none">
              <td style="width: 60px;text-align: center"><input type="checkbox" v-model="dissension" :value="item"></td>
              <!--<td style="width: 100px">{{new Date(item.registerDate).toLocaleDateString()}}</td>-->
              <td style="width: 100px">{{item.registerDate}}</td>
              <td style="width: 200px">{{item.disputeName}}</td>
              <td  @click="caseDetails(item,'dissension')" style="cursor: pointer"　v-html="item.abstract">{{item.abstract}}</td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
<!--{{dissension}}-->
    <div class="confirm">
      <button class="btn btn-primary" @click="PreviousStep">上一步</button>
      <button class="btn btn-primary"  @click="NextStep">下一步</button>
      <!--<button class="btn btn-primary"  @click="next">跳过</button>-->
    </div>
  </div>
</template>

<script>
  export default {
    name: "RelatedCase",
    props: ['RelatedCase','caseId'],
    data() {
      return {
        caseId1:'',
        list1:[],
        list2:[],
        list3:[],
        // list1:this.RelatedCase.dissension_ms,
        // list2:this.RelatedCase.dissension_dx,
        // list3:this.RelatedCase.dissension,
        role: '',
        dissension_ms:[],
        dissension_dx:[],
        dissension:[],
      }
    },
    methods: {
      PreviousStep(){
        // this.$router.push({path: 'KeyWordPage'});
        this.$emit('previous', 3)
      },
      // next(){
      //   this.role = sessionStorage.getItem("role");
      //   if (this.role == 0)
      //   // this.$router.push({path: 'InfoOfModeratorPage'});
      //     this.$emit('next', 3)
      //   else {
      //     // this.$router.push({path: 'MederatorRegister'});
      //     this.$emit('next', 4)
      //   }
      // },
      NextStep() {
        this.role = sessionStorage.getItem("role");
        // let list={
        //   dissension_ms:this.dissension_ms,
        //   dissension_dx:this.dissension_dx,
        //   dissension:this.dissension,
        // }
        // this.$axios({
        //   method:'post',
        //   url:'http://223.3.93.238:8080/RegConflict/similarCasesCollect',
        //   data:{
        //     caseId:this.caseId1,
        //     list:list,
        //   }
        // })
        //   .then((res)=>{
            if (this.role == 0)
            // this.$router.push({path: 'InfoOfModeratorPage'});
              this.$emit('next', 3)
            else {
              // this.$router.push({path: 'MederatorRegister'});
              this.$emit('next', 4)
            }
          // })
        // this.$emit('relatedCase',list)
        }
      ,
      caseDetails(para,kind){
        let route = this.$router.resolve({
          path: '/DisplayCase',
          query:{
            caseName:para.disputeName,
            caseKind:kind,
            caseId:this.caseId1,
          }
        });
        window.open(route.href, '_blank');
        }
    },
    mounted(){
      // this.$axios({
      //   method:'post',
      //   url:'http://223.3.93.238:8080/DisputeWeb/search/similarCases',
      //   data:{
      //     // caseId:sessionStorage.getItem("caseId")
      //     KeyWordList:this.KeyWordList
      //   }
      // })
      //   .then((res)=>{
      //     console.log(res.data)
      //     this.list1=res.data.dissension_ms;
      //     this.list2=res.data.dissension_dx;
      //     this.list3=res.data.dissension;
      //   })
      // console.log(this.RelatedCase)
          this.caseId1=this.caseId;
          this.list1=this.RelatedCase.dissension_ms;
          this.list2=this.RelatedCase.dissension_dx;
          this.list3=this.RelatedCase.dissension;
    },
    watch:{
      RelatedCase:function () {
        this.list1=this.RelatedCase.dissension_ms;
        this.list2=this.RelatedCase.dissension_dx;
        this.list3=this.RelatedCase.dissension;
    },
      caseId:function () {
       this.caseId1=this.caseId;
      },
      dissension_ms:function () {
        let list={
          dissension_ms:this.dissension_ms,
          dissension_dx:this.dissension_dx,
          dissension:this.dissension,
        }
        this.$axios({
          method:'post',
          url:'http://223.3.93.238:8080/RegConflict/similarCasesCollect',
          data:{
            caseId:this.caseId1,
            list:list,
          }
        })
      },
      dissension_dx:function () {
        let list={
          dissension_ms:this.dissension_ms,
          dissension_dx:this.dissension_dx,
          dissension:this.dissension,
        }
        this.$axios({
          method:'post',
          url:'http://223.3.93.238:8080/RegConflict/similarCasesCollect',
          data:{
            caseId:this.caseId1,
            list:list,
          }
        })
      },
      dissension:function () {
        let list={
          dissension_ms:this.dissension_ms,
          dissension_dx:this.dissension_dx,
          dissension:this.dissension,
        }
        this.$axios({
          method:'post',
          url:'http://223.3.93.238:8080/RegConflict/similarCasesCollect',
          data:{
            caseId:this.caseId1,
            list:list,
          }
        })
      },
    },
  }
</script>

<style scoped>
  .info {
    width: 100%;
  }
  li{
    font-weight: bold;
  }
  label {
    margin: 8px auto;
    font-size: 20px;
    color: #3a8dbe;
  }
  #myTabContent{
    height:600px;
    overflow: scroll;
    overflow-x:hidden;
    background-color:rgba(0,0,0,0.02);
    border-left:1px solid rgba(195, 199, 193, 0.84);
    border-right:1px solid rgba(195, 199, 193, 0.84);
    border-bottom:1px solid rgba(195, 199, 193, 0.84);

  }
  .confirm {
    margin-top: 20px;
    text-align: center;
  }
  .confirm button {
    width: 80px;
    margin-left:30px;
    margin-right: 30px;
  }
  button {
    font-size: 16px;
    border-radius: 5px;
  }
</style>



