<template>
  <div>
    <div id="content">
      <button  v-if="currentStageContent.resultOfIdentify.identified==0 && isSuspended==0 " data-toggle="modal" data-target="#confirm" class="btn btn-success">
        建议用户进行医疗损害鉴定
      </button>
      <label class="label1" v-else>{{text}}</label>
      <br>
      <label class="label1">鉴定结果</label> （用户在用户中心上传？）
      <textarea  v-model="currentStageContent.resultOfIdentify.text">{{currentStageContent.resultOfIdentify.text}}</textarea>

      <form @submit="submit" v-if="!fileFlag">
        <div class="field">
          <label class="label1">文件上传</label>
          <input name="file" type="file" multiple @change="getFile($event)">
        </div>
      </form>

      <div v-if="fileFlag">
        <label class="label1" >已上传文件</label>
        <br>
        <label v-for="item in currentStageContent.resultOfIdentify.files" style="">
          <a href="item.url">{{item.name}}</a>
        </label>
      </div>
    </div>

    <div class="foot">
      <button class="btn btn-primary" @click="submit" v-if="isSuspended==1">提交</button>
      <button class="btn btn-primary" @click="skip">跳过</button>
      <button class="btn btn-primary" @click="next" v-if="isSuspended!=1">下一步</button>
    </div>

    <!-- 通知确认框 -->
    <div class="modal fade" id="confirm" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
            <h4 class="modal-title" id="myModalLabel">提示框</h4>
          </div>
          <div class="modal-body" v-if="appoint">是否确认通知？</div>
          <div class="modal-body" v-else>预约后不能做鉴定</div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" @click="InformIndenty" v-if="appoint">确定</button>
            <button type="button" class="btn btn-primary" v-else @click="closeModal">确定</button>
            <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "identification",
    props: ['currentStageContent',"caseId","isSuspended","identiQualify",'stage','appoint'],
    data() {
      return {
        text:'已通知用户进行医疗损害鉴定',
        fileFlag:false,
      //   currentStageContentNow: {
      //   resultOfIdentify:{},//鉴定结果
      //   particopate: [],//参与对象：申请人、被申请人、专家
      //     particopateContact: {
      //     expertChoosed:{
      //       name:'',
      //         id:'',
      //         phone:'',
      //         email:'',
      //     },
      //     mediationPlace: '', //调解时间
      //       mediationTime: '' , //调解地点
      //       //上传的文件
      //       currentFiles: [
      //     ]
      //   }
      // }
      }
    },
    methods: {
      skip(){
        this.$emit('next', 0)
      },
      next() {
        if(this.identiQualify==true &&this.identified==false && this.fileFlag==false)
        {
          alert("还在鉴定当中")
        }
        else
          this.$emit('next', 0)
      },
      submit(){
        event.preventDefault();//取消默认行为
        //创建 formData 对象
        let formData = new FormData();
        // 向 formData 对象中添加文件
        if(this.file!=''){
          formData.append('files',this.file);
        }
        formData.append('caseId',this.caseId)
        formData.append('stage',this.stage)
        formData.append('text',this.currentStageContent.resultOfIdentify.text)
        this.$axios({
          method:'post',
          url:'http://223.3.93.238:8080/DisputeWeb/mediator/resultOfIndent',
          data:formData
        })
        alert('已提交');
        this.isSuspended=0;
        this.$emit('updateIsSuspended', 0)
        // this.fileFlag=true;
        // location.reload();
        this.$emit('next', 0)
      },
      InformIndenty(){
        this.$axios({
          method:'post',
          url:'http://223.3.93.238:8080/DisputeWeb/mediator/InformIndenty',
          data:{
            caseId:this.caseId
          }
        })
        this.isSuspended=1;
        this.$emit('updateIsSuspended', 1)
        this.identified=true;
        $("#confirm").modal("hide");
      },
      getFile(event) {
        this.file = event.target.files[0];
        console.log(this.file);
      },
      closeModal(){
        $("#confirm").modal("hide");
      }
    },
    updated(){
      if(this.currentStageContent.resultOfIdentify.files==undefined)
        this.fileFlag=false;
      console.log(2222222)
      if(this.currentStageContent.resultOfIdentify.files!=undefined)
      {
        console.log(55555555)
        if(this.currentStageContent.resultOfIdentify.files.length>0)
          this.fileFlag=true;
      }
    },
    // watch:{
    //   currentStageContent:function () {
    //     this.currentStageContentNow=this.currentStageContent
    //     console.log(this.currentStageContent)
    //   }
    // }
  }
</script>

<style scoped>
  .label1{
    font-size: 20px;
    color: #3a8dbe;
    margin-top: 10px;
  }
  button {
    font-size: 16px;
    border-radius: 5px;
  }

  #content textarea {
    height:300px;
    width: 100%;
    margin-top: 5px;
    margin-bottom: 10px;
    border-radius: 10px;
    border: none;
    background-color:rgba(0,0,0,0.05);
    overflow: scroll;
    overflow-x: hidden;
  }
  .foot {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    text-align: center;
    position: absolute;
    bottom: 15px;
  }
  .foot button{
    margin-left: 20px;
    margin-right: 20px;
    width: 80px;
  }
</style>



