<template>
  <div>
    <div class="info">
      <div>
        <label>调解员主观评判案件</label>
        <textarea class="detail" placeholder="调解员主观评判案件" v-model="evaluateOfCase"></textarea>
      </div>
      <div>
        <label>调解员对用户画像</label>
        <textarea class="detail" placeholder="调解员对用户画像" v-model="evaluateOfUser"></textarea>
      </div>
      <div>
        <label>调解员提出调解意见</label>
        <textarea  class="detail" placeholder="调解员提出调解意见" v-model="evaluateOfMediation"></textarea>
      </div>
    </div>
    <div class="finish">
      <button class="btn btn-primary" @click="PreviousStep">上一步</button>
      <button class="btn btn-primary" @click="change(0)" data-toggle="modal" data-target="#confirm">提交</button>
      <button class="btn btn-primary" @click="change(1)" data-toggle="modal" data-target="#confirm">跳过</button>
    </div>

    <!-- 通知确认框 -->
    <div class="modal fade" id="confirm" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
            <h4 class="modal-title" id="myModalLabel">确认框</h4>
          </div>
          <div class="modal-body" v-if="flag==0">是否提交？</div>
          <div class="modal-body" v-if="flag==1">是否跳过？</div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" @click="ok" v-if="flag==0">确定</button>
            <button type="button" class="btn btn-primary" @click="skip" v-if="flag==1">确定</button>
            <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "ModeratorRegister",
    data() {
      return {
        evaluateOfCase:'',
        evaluateOfUser:'',
        evaluateOfMediation:'',
        flag:'',
      }
    },
    methods: {
      change(para){
        this.flag=para;
      },
      skip() {
        $("#confirm").modal("hide");
        alert('已提交')
        location.reload();
      },
      ok() {
        let moderatorRegister={
          evaluateOfCase: this.evaluateOfCase,
          evaluateOfUser:this.evaluateOfUser,
          evaluateOfMediation:this.evaluateOfMediation
        }
        this.$axios({
          method: 'post',
          url: 'http://223.3.93.238:8080/DisputeWeb/postEvaluateOfMediator',
          data: {
            caseId:sessionStorage.getItem('caseId'),
            moderatorRegister:moderatorRegister,
          }
        })
        $("#confirm").modal("hide");
        alert('已提交')
        // this.$emit('moderatorRegister', moderatorRegister)
        location.reload();
      },

      PreviousStep(){
        // this.$router.push({path: 'BasicDivideInfo'});
        this.$emit('previous', 4)
      },
    }
  }
</script>

<style scoped>
  .info {
    width: 100%;
  }
  .detail{
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    height:180px;
    overflow: scroll;
    overflow-x:hidden;
    padding: 10px 20px;
    border-radius: 10px;
    background-color:rgba(0,0,0,0.03);
  }
  label{
    margin: 8px auto;
    font-size: 20px;
    color: #3a8dbe;
  }
  .finish{
    text-align: center;
    margin-top: 20px;
  }

  button{
    font-size: 16px;
    outline: none;
    border: none;
    border-radius: 5px;
    width: 80px;
  }

</style>



