<template>
  <div>
    <div class="upload">
      <button class="btn btn-primary">视频调解</button>
      <form @submit="submit">
        <div class="field">
          <label class="label1">音频文件上传</label>
          <input name="file" type="file" multiple @change="getFile($event)">
        </div>

        <div class="field">
          <label class="label1">调解记录上传</label>
          <input name="file2" type="file" multiple @change="getFile2($event)">
        </div>
        <!--<button class="btn btn-primary" type="submit" value="提交" >提交</button>-->
      </form>

      <div class="Uploaded" v-if="fileFlag">
        <label class="label1">已上传文件</label>
        <br>
        <label v-for="item in currentStageContent.particopateContact.currentFiles">
          <a href="item.url" style="margin-right: 50px">{{item.url}}</a>
        </label>
      </div>

      <div class="submit">
        <button class="btn btn-primary" @click="submit" value="提交" >提交</button>
      </div>

    </div>
    <div class="foot">
      <button class="btn btn-primary"  @click="previous">上一步</button>
      <button class="btn btn-primary" @click="changeCurrentOperation(0)" data-toggle="modal" data-target="#confirm">调解成功</button>
      <button class="btn btn-primary" @click="changeCurrentOperation(1)" data-toggle="modal" data-target="#confirm">再次调解</button>
      <button class="btn btn-primary" @click="changeCurrentOperation(2)" data-toggle="modal" data-target="#confirm">调解失败</button>
    </div>

    <!-- 通知确认框 -->
    <div class="modal fade" id="confirm" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
            <h4 class="modal-title" id="myModalLabel">确认框</h4>
          </div>
          <div class="modal-body" v-if="currentOperation==0">此案件调解成功？</div>
          <div class="modal-body" v-if="currentOperation==1">本轮调解失败,重新进入调解？</div>
          <div class="modal-body" v-if="currentOperation==2">此案件调解失败？</div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" @click="confirmOperation">确定</button>
            <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "to-mediating",
    props:["caseId","currentStageContent",'identiQualify'],
    data(){
      return{
        file:'',
        file2:'',
        fileFlag:false,
        currentOperation:'',
      }
    },
    methods: {
      getFile(event) {
        this.file = event.target.files[0];
        console.log(this.file);
      },
      getFile2(event) {
        this.file2 = event.target.files[0];
        console.log(this.file2);
      },
      submit() {
        // event.preventDefault();//取消默认行为
        //创建 formData 对象
        let formData = new FormData();
        // console.log(this.file);
        // 向 formData 对象中添加文件
        if(this.file!=''){
          formData.append('files',this.file);
        }
        if(this.file2!=''){
          formData.append('files',this.file2);
        }
        formData.append('caseId',this.caseId)
        this.$axios({
          method:'post',
          url:'http://223.3.93.238:8080/DisputeWeb/Mediation/MediationUpload',
          data:formData,
        })
        alert('提交成功，请选择调解结果')
      },
      success(){
        this.$axios({
          method:'post',
          url:'http://223.3.93.238:8080/DisputeWeb/mediator/MediationSuccess',
          data:{caseId:this.caseId},

        })
        // alert('提交成功')
      }  ,
      reMediation(){
        this.$axios({
          method:'post',
          url:'http://223.3.93.238:8080/DisputeWeb/mediator/reMediation',
          data:{caseId:this.caseId}
        })
          .then(res=>{
            this.$emit('updateMessage',res.data.data)
            if(this.identiQualify==false)
              this.$emit('previous', 2)
            else
              this.$emit('previous', 1)
            location.reload();
          })

      },
      failure(){
        this.$axios({
          method:'post',
          url:'http://223.3.93.238:8080/DisputeWeb/mediator/MediationFailure',
          data:{caseId:this.caseId}
        })
        // alert('提交成功')
      },
      changeCurrentOperation(para){
        this.currentOperation=para;
      },
      confirmOperation(){
        if( this.currentOperation==0)
          this.success();
        else if( this.currentOperation==1)
          this.reMediation();
        else
          this.failure();
        $("#confirm").modal("hide");
      },
      previous(){
        this.$emit('previous', 2)
      },
    },
    mounted(){
      // console.log(1111111)
      // console.log(this.currentStageContent.particopateContact.currentFiles)
        if(this.currentStageContent.particopateContact.currentFiles.length>0){
          this.fileFlag=true;
        }

    }
  }
</script>

<style scoped>
  .upload{
    height:550px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 3%;
    padding-top: 2%;
    background-color:rgba(0,0,0,0.05);
    border-radius:5px ;
  }
  .field{
    margin-top: 20px;
    /*text-align: center;*/
    margin-bottom: 50px;
  }
  .label1{
    font-size: 18px;
    color: #3a8dbe;
  }
  .foot {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    text-align: center;
    position: absolute;
    bottom: 15px;
  }
  .foot button{
    margin-left: 20px;
    margin-right: 20px;
  }
  button {
    font-size: 16px;
    border-radius: 5px;
  }

  .submit {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    /*position: absolute;*/
    /*bottom: 30px;*/
  }
  .submit button{
    width: 80px;
  }
</style>



