<template>
  <div>
    <div class="top">
    <span><label class="title">{{caseName}}</label>
    <label :class="{collectClass:collect==true}" @click="changeCollect" style="cursor: pointer">★</label></span>
    </div>
    <div class="content">
      <div v-if="caseKind=='dissension_ms'">
        <label class="label1">纠纷类型：</label>{{caseContent_ms.disputeType}}
        <br>
        <label class="label1">登记时间：</label>{{caseContent_ms.registerDate}}
        <br>
        <label class="label1">涉事人员：</label>{{caseContent_ms.personName}}
        <br>
        <label class="label1">涉事医疗机构：</label>{{caseContent_ms.hospitalName}}
        <br>
        <label class="label1">法院认为：</label><p v-html="caseContent_ms.courtThink">{{caseContent_ms.courtThink}}</p>
        <label class="label1">审判结果：</label><p v-html="caseContent_ms.judgeDecision">{{caseContent_ms.judgeDecision}}</p>
      </div>
      <div v-else-if="caseKind=='dissension_dx'">
        <label class="label1">纠纷类型：</label>{{caseContent_dx.disputeType}}
        <br>
        <label class="label1">发生地点：</label>{{caseContent_dx.disputeLocation}}
        <br>
        <label class="label1">责任单位：</label>{{caseContent_dx.institution}}
        <br>
        <label class="label1">调解员：</label>{{caseContent_dx.staffName}}
        <br>
        <label class="label1">简要情况：</label><p v-html="caseContent_dx.abstract">{{caseContent_dx.abstract}}</p>
        <label class="label1">调解结果：</label><p v-html="caseContent_dx.disputeResolution">{{caseContent_dx.disputeResolution}}</p>
        <label class="label1">案例点评：</label><p v-html="caseContent_dx.comment">{{caseContent_dx.comment}}</p>
        <label class="label1">适用法律：</label><p v-html="caseContent_dx.relateLaw">{{caseContent_dx.relateLaw}}</p>
      </div>
      <div v-else="caseKind=='dissension'">
        <label class="label1">编号：</label>{{caseContent.disputeNO}}
        <br>
        <label class="label1">区域划分：</label>{{caseContent.district}}
        <br>
        <label class="label1">纠纷类型：</label>{{caseContent.disputeType}}
        <br>
        <label class="label1">纠纷区域：</label>{{caseContent.disputeArea}}
        <br>
        <label class="label1">发生地点：</label>{{caseContent.disputeLocation}}
        <br>
        <label class="label1">案情评估：</label>{{caseContent.evaluation}}
        <br>
        <label class="label1">激烈程度：</label>{{caseContent.fierceDegree}}
        <br>
        <label class="label1">纠纷等级：</label>{{caseContent.disputeDegree}}
        <br>
        <label class="label1">受理时间：</label>{{caseContent.acceptDate}}
        <br>
        <label class="label1">纠纷来源：</label>{{caseContent.disputeOrigin}}
        <br>
        <label class="label1">涉事人员：</label>{{caseContent.personName}}
        <br>
        <label class="label1">涉事医疗机构：</label>{{caseContent.hospitalName}}
        <br>
        <label class="label1">涉及人数：</label>{{caseContent.disputePerson}}
        <br>
        <label class="label1">涉及金额：</label>{{caseContent.compensation}}
        <br>
        <label class="label1">简要情况：</label><p v-html="caseContent.abstract">{{caseContent.abstract}}</p>
        <label class="label1">调解结果：</label>{{caseContent.disputeResolution}}
        <br>
        <label class="label1">调解成功时间：</label>{{caseContent.resolutionDate}}
        <br>
        <label class="label1">达成协议内容：</label><p v-html="caseContent.agreement">{{caseContent.agreement}}</p>
        <label class="label1">责任单位：</label>{{caseContent.institution}}
        <br>
        <label class="label1">调解员：</label>{{caseContent.staffName}}
        <br>
        <!--<label class="label1">标签：</label>{{caseContent.disputeTag}}-->
        <!--<br>-->
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "DisplayCase",
    data() {
      return {
        collect:false,
        caseKind: this.$route.query.caseKind,
        caseName: this.$route.query.caseName,
        caseId:this.$route.query.caseId,
        caseContent_ms: {
          registerDate: '',
          personName: '',
          hospitalName: '',
          length: '',
          disputeName: '',
          judgeDecision: '',
          disputeTag: '',
          disputeType: '',
          courtThink: '',
        },
        caseContent_dx: {
          registerDate:'',
          comment: '',
          length: '',
          disputeResolution: '',
          disputeLocation:'',
          disputeName: '',
          abstract:  '',
          disputeTag: '',
          disputeType:'',
          staffName:'',
          relateLaw: '',
          institution: '',
        },
        caseContent: {
          disputeNO: '',
          district: '',
          disputeName: '',
          evaluation: '',
          fierceDegree: '',
          disputePerson: '',
          compensation: '',
          disputeDegree: '',
          acceptDate: '',  //受理时间
          disputeOrigin: '',
          disputeType: '',  //纠纷类型
          disputeArea: '',//纠纷区域
          disputeLocation: '',  //发生地点
          disputeResolution: '',
          resolutionDate: '',
          institution: '',  //责任单位
          staffName: '',   //调解员
          agreement: '',  //协议内容
          disputeTag: '',
          personName:'',  //涉事人员
          hospitalName:'',  //涉事医疗机构
          length: '',
          abstract:'', //简要情况
        }
      }
    },
    methods: {
      changeCollect(){
        this.collect=!this.collect;
        this.collectClick();
      },
      collectClick(){

       if(this.collect==true)
       {
         this.$axios({
           method:'post',
           url:'http://223.3.93.238:8080/DisputeWeb/caseCollect',
           data:{
             caseId:this.caseId,
             caseName:this.caseName,
             type:this.caseKind
           }
         })
       }
       else {
         this.$axios({
           method:'post',
           url:'http://223.3.93.238:8080/DisputeWeb/cancelCaseCollect',
           data:{
             caseId:this.caseId,
             caseName:this.caseName,
             type:this.caseKind
           }
         })
       }
      }
    },
    mounted() {
      console.log(this.caseId)
      switch (this.caseKind) {
        case 'dissension_ms':
          this.$axios({
            method:'post',
            url:'http://223.3.93.238:8080/DisputeWeb/search/caseDetails',
            data:{
              caseId:this.caseId,
              caseName:this.caseName,
              type:this.caseKind
            }
          })
            .then((res)=>{
              this.caseContent_ms=res.data.result;
              this.collect=res.data.collect;
            });
          break;
        case 'dissension_dx':
          this.$axios({
            method:'post',
            url:'http://223.3.93.238:8080/DisputeWeb/search/caseDetails',
            data:{
              caseId:this.caseId,
              caseName:this.caseName,
              type:this.caseKind
            }
          })
            .then((res)=>{
              this.caseContent_dx=res.data.result;
              this.collect=res.data.collect;
            });
          break;
        case 'dissension':
          this.$axios({
            method:'post',
            url:'http://223.3.93.238:8080/DisputeWeb/search/caseDetails',
            data:{
              caseId:this.caseId,
              caseName:this.caseName,
              type:this.caseKind
            }
          })
            .then((res)=>{
              this.caseContent=res.data.result;
              this.collect=res.data.collect;
            });
          break;
      }
    }
  }
</script>

<style scoped>
  .top{
    margin-top: 15px;
    text-align: center;
    font-size: 30px;
  }
.title{
  color: #3a8dbe;
}
  .content{
    border: 5px ridge rgba(182, 182, 182, 0.63);
    margin: 10px auto;
    width: 70%;
    border-radius: 15px;
    padding: 10px 15px;
    background: rgba(219, 219, 219, 0.73);
    font-size: 18px;
  }
.label1 {
  margin: 8px auto;
  font-size: 20px;
  color: #3a8dbe;
}
.collectClass{
    color: #deb610;
  font-weight: bold;
}
</style>



