<script>
  import ViewPage from '../../components/ViewPage'
  import Dialog from '../../components/Dialog'
  import Vue from 'Vue'

  export default {
    name: "MediationHall",
    components: {
      ViewPage,
      AcceptApplyDialog: Dialog,
      RefuseApplyDialog: Dialog,
      InquireDialog: Dialog,
      // ShowAllTextDialog:Dialog,
    },
    data() {
      return {
        data: [],
        filterStatus: '',

        searchStr: '',
        currentStatus1: ['申请中', '等待受理', '等待分配'],
        currentStatus2: ['申请中', '等待受理', '等待分配','撤销申请中','撤销调解中'],
        statuses: ['申请中','等待分配', '调解中','调解失败', '结案中', '已结案', '撤销申请', '撤销调解','立案失败','等待受理','撤销申请中','撤销调解中','申请更换调解员中'],
        statusColors: ['warning', 'warning', 'success','danger', 'success', 'info', 'info', 'info','danger','warning','warning','warning','warning'],
        // sortProp: '',
        // sortOrder: 'descending',
        multipleSelection: [],
        currentPage: 1,
        currentPageSize: 10,
        PageSizes: [2, 5, 10],
        totalPages: 1,
        authorityConfirm: 0,
        acceptApplyShow: false,
        refuseApplyShow: false,
        allTextShow: false,
        inquireShow: false,
        tempCaseId: '',
        tempCase:'',
        startTime: '',
        endTime: '',
        acceptReason:'',
        reasonForApply: '',
        reasonForRefuse: '',

        repealApplyReason:'',
        repealApplyResult:'',
        repealMediationReason:'',
        repealMediationResult:'',
        // reasonForRefuse: '',
        currentprocessbar: '',
        inquireResult: '',
        inquireHistory: [{
          hospital: '',
          inquireDate: '',
          mediatorName: '',
          inquireResult: '',
          inquireReplyPerson: '',
          otherMessage: '',
        }],
        // tempText:'',
        instituteMessage: [{
          name: '',
          fixedTele: '',
          contactPerson: '',
          contactPhone: ''
        }
        ],
        isFinished: '',
        inquireMessage: [
          {
            hospital: '',
            inquireDate: '',
            inquireReplyPerson: '',
            inquireResult: '',
            otherMessage: '',
          },
        ],
        file: '',
        result: '',
        applyFile: {
          files: ['http://pe93soaos.bkt.clouddn.com/common/%E5%8D%97%E4%BA%AC%E5%B8%82%E5%8C%BB%E6%82%A3%E7%BA%A0%E7%BA%B7%E4%BA%BA%E6%B0%91%E8%B0%83%E8%A7%A3%E5%A7%94%E5%91%98%E4%BC%9A%EF%BC%88%E5%91%8A%E7%9F%A5%E4%B9%A6%EF%BC%89%E6%9C%89%E6%9D%83%E5%88%A9%E3%80%81%E4%B9%89%E5%8A%A1.docx']
        },
        currentInsititue: 0,
      }
    },
    mounted() {
      if (sessionStorage.getItem("authorityConfirm") == '1')
        this.authorityConfirm = 1;
      else
        this.authorityConfirm = 0
      this.$axios.post('http://223.3.93.238:8080/DisputeWeb/mediator/GetMediationHallData',
        {
          id: sessionStorage.getItem("id"),
          size: this.currentPageSize,
          page: this.currentPage,
          filterStatus: this.filterStatus,
          startTime: this.startTime,
          endTime: this.endTime,
          authorityConfirm:this.authorityConfirm,
        })
        .then((res) => {
          this.data = res.data.data.mediationHallDataFormList;
          this.totalPages = res.data.data.totalPages;
          //将data中的applicant、数组转化成字符串
          this.data.forEach((item) => {
            item.applicant = item.applicant.join('  ');
          });
          this.data.forEach((item) => {
            item.respondent = item.respondent.join('  ');
          });
          this
        })
        .catch((error) => {
          console.log(error)
        });

    },

    methods: {
      RepealApply() {
        this.$axios({
          method: 'post',
          url: 'http://223.3.93.238:8080/DisputeWeb/feedBackCaseCancelApply',
          data: {
            caseId: this.tempCaseId,
            id: sessionStorage.getItem("id"),
            repealApplyReason:this.repealApplyReason,
            repealApplyResult:this.repealApplyResult,
          },
        })
          .then((res) => {
            let nextStatus=(this.repealApplyResult==1)?6:0
            if (res.data.code >= 0) {
              let index = this.data.findIndex(item => {
                return item.caseId == this.tempCaseId
              })
              // console.log(this.data[index])
              Vue.set(this.data, index, {
                date: this.data[index].date,
                name: this.data[index].name,
                caseId: this.data[index].caseId,
                status: nextStatus,
                applicant: this.data[index].applicant,
                respondent: this.data[index].respondent,
                fordebarb: this.data[index].fordebarb,
                userUpload:this.data[index].userUpload,
              });
              $("#RepealApply").modal("hide");
            }
          })
      },
      RepealMediation() {
        this.$axios({
          method: 'post',
          url: 'http://223.3.93.238:8080/DisputeWeb/feedBackCaseCancelMediate',
          data: {
            caseId: this.tempCaseId,
            id: sessionStorage.getItem("id"),
            repealMediationReason:this.repealMediationReason,
            repealMediationResult:this.repealMediationResult,
          },
        })
          .then((res) => {
            let nextStatus=(this.repealMediationResult==1)?7:2
            if (res.data.code >= 0) {
              let index = this.data.findIndex(item => {
                return item.caseId == this.tempCaseId
              });
              // console.log(this.data[index])
              Vue.set(this.data, index, {
                date: this.data[index].date,
                name: this.data[index].name,
                caseId: this.data[index].caseId,
                status: nextStatus,
                applicant: this.data[index].applicant,
                respondent: this.data[index].respondent,
                fordebarb: this.data[index].fordebarb,
                userUpload:this.data[index].userUpload,
              });
              $("#RepealMediation").modal("hide");
            }
          })
      },
      changeTempCase(para) {
        this.tempCaseId = para;
      },
      ForDebarb() {
        this.$axios({
          method: 'post',
          url: 'http://223.3.93.238:8080/DisputeWeb/mediator/fordebarb',
          data: {
            id: sessionStorage.getItem("id"),
            caseId: this.tempCaseId
          }
        })
          .then((res) => {
            if (res.data.code >= 0) {
              let index = this.data.findIndex(item => {
                return item.caseId == this.tempCaseId
              })
              console.log(this.data[index])
              Vue.set(this.data, index, {
                date: this.data[index].date,
                name: this.data[index].name,
                caseId: this.data[index].caseId,
                status: this.data[index].status,
                applicant: this.data[index].applicant,
                respondent: this.data[index].respondent,
                userUpload:this.data[index].userUpload,
                fordebarb: 1,
              })
              // this.closeAccept()
              this.acceptApplyShow = false
              // console.log(this.data[index])
            }
          })
          .catch((error) => {
            console.log(error)
          });
        $("#forDebarb").modal("hide");

      },
      ForCaseDetail(para) {
        let caseState = this.statuses[para.status];
        let caseName = para.name;
        let caseId = para.caseId;
        let route = this.$router.resolve({
          path: '/CaseDetailPage',
          query: {
            prePath: this.$route.path,
            caseName: caseName,
            caseState: caseState,
            caseId: caseId
          }
        });
        window.open(route.href, '_blank');
      },
      refresh() {
        location.reload()
      },
      getFile(event) {
        this.file = event.target.files[0];
      },
      // // 排序
      // sortChange(column) {
      //   this.sortProp = column.prop
      //   this.sortOrder = column.order
      // },
      // //起始时间控制
      // timeControl(){
      //   // console.log(this.startTime>this.endTime)
      //   if(this.startTime!=''&&this.endTime!=''&&this.startTime>this.endTime) {
      //     // console.log(this.startTime>this.endTime)
      //     alert("起始时间不可大于结束时间")
      //     this.startTime=''
      //     this.endTime=''
      //   }
      // },
      //分页
      PreviousPage() {
        if (this.currentPage > 1) {
          this.currentPage = this.currentPage - 1;
          this.currentPageContent();
        }
      },
      NextPage() {
        if (this.currentPage < this.totalPages) {
          this.currentPage = this.currentPage + 1;
          this.currentPageContent();
        }
      },
      changeStatus() {
        this.currentPage = 1;
        this.currentPageContent();
      },
      currentPageContent() {
        if (this.startTime != '' && this.endTime != '' && this.startTime > this.endTime) {
          alert("起始时间不可大于结束时间")
          this.startTime = ''
          this.endTime = ''
          return;
        }
        if (this.currentPage <= 0) {
          alert("页数必须大于0")
          this.currentPage = 1;
        }
        //请求案件进展列表
        this.$axios.post('http://223.3.93.238:8080/DisputeWeb/mediator/GetMediationHallData',
          {
            id: sessionStorage.getItem("id"),
            size: this.currentPageSize,
            page: this.currentPage,
            filterStatus: this.filterStatus,
            startTime: this.startTime,
            endTime: this.endTime,
            authorityConfirm:this.authorityConfirm,
          })
          .then((res) => {
            this.data = res.data.data.mediationHallDataFormList;
            this.totalPages = res.data.data.totalPages
            //将data中的applicant、respondent数组转化成字符串
            this.data.forEach((item) => {
              item.applicant = item.applicant.join('  ');
            })
            this.data.forEach((item) => {
              item.respondent = item.respondent.join('  ');
            })
          })
          .catch((error) => {
            console.log(error)
          });
      },
      //通过申请
      submitAccept() {
        // event.preventDefault();//取消默认行为
        //创建 formData 对象
        let formData = new FormData();
        // 向 formData 对象中添加文件
        if (this.file != '') {
          formData.append('file', this.file);
        }
        formData.append('caseId', this.tempCaseId)
        formData.append('acceptReason',this.acceptReason)
        formData.append('id', sessionStorage.getItem("id"))
        formData.append('result', this.result);
        this.$axios({
          method: 'post',
          url: 'http://223.3.93.238:8080/DisputeWeb/DisposeApply',
          data: formData,
          //   {
          //   id: sessionStorage.getItem("id"),
          //   caseId: this.tempCaseId,
          //   result: 1,
          //   // reasonForApply:this.reasonForApply
          // }
        })
          .then((res) => {
            let nextStatus = 9;
            if (this.result == 1)
              nextStatus = 1;
            else
              nextStatus = 8;
            if (res.data.code >= 0) {
              let index = this.data.findIndex(item => {
                return item.caseId == this.tempCaseId
              })
              console.log(this.data[index])
              Vue.set(this.data, index, {
                date: this.data[index].date,
                name: this.data[index].name,
                caseId: this.data[index].caseId,
                status: nextStatus,
                applicant: this.data[index].applicant,
                respondent: this.data[index].respondent,
                fordebarb: this.data[index].fordebarb,
                userUpload:this.data[index].userUpload,
              });
              // this.closeAccept()
              this.acceptApplyShow = false
              console.log(this.data[index])
            }
          })
          .catch((error) => {
            console.log(error)
          });
        // this.acceptApplyShow = false
      },
      closeAccept() {
        this.acceptApplyShow = false
      },
      acceptApply(para) {
        this.tempCase=para;
        this.tempCaseId = para.caseId;
        this.acceptApplyShow = true
        // this.CurrentCaseResult=1;
        // let href1='http://pe93soaos.bkt.clouddn.com/common/%E5%8D%97%E4%BA%AC%E5%B8%82%E5%8C%BB%E8%B0%83%E5%A7%94%E6%A1%88%E4%BB%B6%E5%8F%97%E7%90%86%E9%80%9A%E7%9F%A5%E4%B9%A6.doc'
        // window.location.href=href1;
        // window.open(href1,'_blank')

        // let link = document.createElement('a');
        // link.style.display = 'none';
        // link.href = 'http://pe93soaos.bkt.clouddn.com/common/%E5%8D%97%E4%BA%AC%E5%B8%82%E5%8C%BB%E8%B0%83%E5%A7%94%E6%A1%88%E4%BB%B6%E5%8F%97%E7%90%86%E9%80%9A%E7%9F%A5%E4%B9%A6.doc'
        // link.setAttribute('download', '南京市医调委案件受理通知书.doc');
        // document.body.appendChild(link);
        // link.click()

      },
