<template>
  <div class="detail">
    <h3
      style="text-align:center;width: 100%;margin-left: auto;margin-right: auto;font-family: 'Microsoft YaHei';color: #3a8dbe">
      {{caseName}}
    </h3>

    <div>
      <label class="label1">用户登记信息</label>
      <div class="table-responsive">
        <table class="table table-responsive table-bordered">
          <thead>
          <tr>
            <th></th>
            <th v-for="(item,index) in  registerData.stage">{{item.name}}</th>
          </tr>
          </thead>
          <tbody>
          <tr>
            <td>涉事人员</td>
            <td :colspan="registerData.stage.length">{{registerData.involvedPeople}}</td>
          </tr>
          <tr>
            <td>涉事医院</td>
            <td v-for="(item,index) in  registerData.stage">{{item.involvedInstitute}}</td>
          </tr>
          <tr>
            <td>可能过失行为</td>
            <td v-for="(item,index) in  registerData.stage">{{item.behaviorList}}</td>
          </tr>
          <tr>
            <td>医疗前病症</td>
            <td v-for="(item,index) in  registerData.stage">{{item.diseasesBefore}}</td>
          </tr>
          <tr>
            <td>医疗后病症</td>
            <td v-for="(item,index) in  registerData.stage">{{item.diseaseAfter}}</td>
          </tr>
          <tr>
            <td>损害结果</td>
            <td v-for="(item,index) in  registerData.stage">{{item.resultOfDamage}}</td>
          </tr>
          <tr>
            <td>主要纠纷场景</td>
            <td :colspan="registerData.stage.length">{{registerData.mainRecSatge}}</td>
          </tr>
          <tr>
            <td>索赔金额</td>
            <td :colspan="registerData.stage.length">{{registerData.claimAmount}}</td>
          </tr>
          <tr>
            <td>诉求</td>
            <td :colspan="registerData.stage.length">{{registerData.require}}</td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div>
      <label class="label1">相关案例推荐</label>
      <div class="table-responsive">
        <table class="table table-responsive table-bordered table-hover">
          <tbody>
          <tr :colspan="registerData.relatedCases.dissension_ms.length" v-if="registerData.relatedCases.dissension_ms.length>0">
            <td><label class="label2">裁判文书</label></td>
          </tr>
          <tr v-for="item in registerData.relatedCases.dissension_ms">
            <td style="width: 100px">{{item.registerDate}}</td>
            <td style="width: 200px">{{item.disputeName}}</td>
            <td  @click="caseDetails(item,'dissension_ms')" style="cursor: pointer"　v-html="item.abstract">{{item.abstract}}</td>
          </tr>
          <tr :colspan="registerData.relatedCases.dissension_ms.length" v-if="registerData.relatedCases.dissension_dx.length>0">
            <td ><label class="label2">典型案例</label></td>
          </tr>
          <tr v-for="item in registerData.relatedCases.dissension_dx" style="border: none" >
            <td style="width: 100px">{{item.registerDate}}</td>
            <td style="width: 200px">{{item.disputeName}}</td>
            <td  @click="caseDetails(item,'dissension_dx')" style="cursor: pointer"　v-html="item.abstract">{{item.abstract}}</td>
          </tr>
          <tr :colspan="registerData.relatedCases.dissension.length"  v-if="registerData.relatedCases.dissension.length>0">
            <td ><label class="label2">纠纷调解</label></td>
          </tr>
          <tr v-for="item in registerData.relatedCases.dissension" style="border: none">
            <td style="width: 100px">{{item.registerDate}}</td>
            <td style="width: 200px">{{item.disputeName}}</td>
            <td  @click="caseDetails(item,'dissension')" style="cursor: pointer"　v-html="item.abstract">{{item.abstract}}</td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div>
      <label class="label1">案件具体进展</label>
      <div class="table-responsive"></div>
      <table class="table table-responsive table-bordered">
        <thead>
        <tr >
          <th></th>
          <th colspan="3">用户</th>
          <th colspan="3">调解员</th>
          <th colspan="3">管理者</th>
          <th colspan="3">院方</th>
        </tr>
        </thead>
        <tbody>
        <tr>
          <td></td>
          <td>用户评价</td>
          <td>资料</td>
          <td>用户操作</td>
          <td>调解员发言</td>
          <td>资料</td>
          <td>调解员操作</td>
          <td>管理者发言</td>
          <td>资料</td>
          <td>管理者操作</td>
          <td>院方联系方式</td>
          <td>院方发言</td>
          <td>资料</td>
        </tr>
        <tr>
          <td>登记</td>
          <td></td>
          <td></td>
          <td></td>
          <td>
            <!--{{registerData.调解员主观评判}}-->
          </td>
          <td></td>
          <td>
            问询记录
          </td>
          <td>
            <!--<button class="btn btn-primary">通过受理</button><br/><br/>-->
            <!--<button class="btn btn-primary">拒绝受理</button>-->
          </td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>申请中</td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>调解中</td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>结案中</td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>已结案</td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        </tbody>
      </table>
    </div>

    <div class="return">
      <button class="btn btn-primary" @click="back">返回</button>
    </div>

  </div>


</template>

<script>
  export default {
    name: "CaseDetail",
    data() {
      return {
        caseId:this.$route.query.caseId,
        caseName: this.$route.query.caseName,
        registerData: {
          involvedPeople:'',
          stage: [
            {
              resultOfDamage: "",
              name: '',
              involvedInstitute:'',
              diseasesBefore: "",
              diseaseAfter: "",
              behaviorList:'',
            }
          ],
          mainRecSatge: '',//主要纠纷场景
          require: '',  //诉求
          claimAmount: '',//索赔金额
          relatedCases: {
            dissension_ms: [],
            dissension_dx: [],
            dissension: [],
          },
          InquireText:''
        },
      }
    },
    methods: {
      back(){
        this.$router.push({path: this.prepath})
      },
      submitInquire(){
        this.$axios({
          method:'post',
          url:'http://223.3.93.238:8080/DisputeWeb/detail/InquireInstitute',
          data:{
            caseId:this.caseId,
            inquireText:this.InquireText
          }
        })
      },
      caseDetails(para,kind){
        console.log(kind);
        let route = this.$router.resolve({
          path: '/DisplayCase',
          query:{
            caseName:para.disputeName,
            caseKind:kind,
            caseId:this.caseId
          }
        });
        window.open(route.href, '_blank');
      }
    },
    mounted(){
      this.$axios({
        method:'post',
        url:'http://223.3.93.238:8080/DisputeWeb/caseDetail',
        data:{
          caseId:this.caseId
        }
      })
        .then((res) => {
          this.registerData=res.data.data;
          this.registerData.involvedPeople= this.registerData.involvedPeople.join('  ');
          this.registerData.stage.forEach((item)=>{
            item.behaviorList= item.behaviorList.join('  ');
          })
          this.registerData.stage.forEach((item)=>{
            item.behaviorList= item.behaviorList.join('  ');
          })
        })
        .catch((error) => {
          console.log(error)
        });

    }
  }
</script>

<style scoped>
  .detail{
    width: 98%;
    margin-left: auto;
    margin-right: auto;
  }
  .label1 {
    margin: 10px auto;
    font-size: 18px;
    color: #3a8dbe
  }
  .label2 {
    font-size: 17px;
    color: #3a8dbe
  }
  button {
    border-radius: 10px;
  }

  table {
    font-size: 16px;
  }

  th{
    text-align: center;
  }
  .return {
    margin: 30px auto;
    text-align: center;
  }
</style>



