<template>
  <div>
    <div class="container">
      <div class="row">
        <div class="col-lg-8 col-md-8 col-sm-8 col-xs-8">
          <label>纠纷要素预览</label>
          <div class="info">
            <div class="tableInfo">
              <table class="table  table-bordered table-hover">
                <thead>
                <tr>
                  <th style="text-align:center; color: #3a8dbe;">疗程名称</th>
                  <th style="text-align:center; color: #3a8dbe;">纠纷大类</th>
                  <th style="text-align:center; color: #3a8dbe;">纠纷子项</th>
                  <th style="text-align:center; color: #3a8dbe;">纠纷内容</th>
                </tr>
                </thead>

                <tbody v-for="(item,index) in list">
                <tr style="text-align: center;font-size: 16px;font-weight: lighter;height: 30px">
                  <td style="width:100px">
                    <input type="text" v-model="item.stageName" class="inputClass">
                  </td>
                  <td style="width: 150px" >
                    <input type="text" v-model="item.regConfKind" class="inputClass">
                  </td>
                  <td style="width:200px">
                    <input type="text" v-model="item.subRegConfKind" class="inputClass">
                  </td>
                  <td>
                    <input type="text" v-model="item.value" class="inputClass">
                  </td>
                  <td style="width:200px">
                    <button class="btn-primary up" @click="up(index)">↑</button>
                    <button class="btn-primary down" @click="down(index)">↓</button>
                    <button class="btn-primary deleteword" @click="deleteword(index)">×</button>
                  </td>
                </tr>
                </tbody>
              </table>
            </div>
            <div class="ButtonID">
              <button class="btn btn-primary" @click="AddLine()">添加</button>
              <!--<button class="btn btn-primary" @click="DeleteLine()">删除</button>-->
            </div>
          </div>
        </div>
        <div class="col-lg-4 col-md-4 col-sm-4 col-xs-4">
          <label>简要情况</label>
            <div class="brief">
        <textarea id="show" v-model="brief" >

        </textarea>
          </div>
        </div>
      </div>


    </div>
    <div class="confirm">
      <button class="btn btn-primary" @click="PreviousStep">上一步</button>
      <button class="btn btn-primary" @click="NextStep">下一步</button>
      <button class="btn btn-primary" @click="next">跳过</button>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'

  export default {
    name: "KeyWord",
    props: ['caseId'],
    data() {
      return {
        list: [
        ],
        brief:'',
        // list: this.KeyWordList,
        // brief:this.Brief,
        caseId1:''
      }
    },
    methods: {
      PreviousStep(){
        // this.$router.push({path: 'BasicDivideInfo'});
        this.$emit('previous', 2)
      },
      next(){
        this.$emit('next', 3)
      },
      NextStep() {
        // this.$router.push({path: 'RelatedCasePage'})
        // this.$axios({
        //   method: 'post',
        //   url: 'http://223.3.93.238:8080/DisputeWeb/search/postKeyWordList',
        //   data: {
        //     id:sessionStorage.getItem('id'),
        //     // caseId:sessionStorage.getItem("caseId"),
        //     list:this.list
        //   }
        // })
        //   .then(res=>{
        //     this.$emit('next', 2)
        //   })
        // this.$emit('keyWordList', this.list,this.brief)
        this.$axios({
          method:'post',
          url:'http://223.3.93.238:8080/DisputeWeb/search/similarCases',
          data:{
            caseId:this.caseId1,
            KeyWordList:this.list
          }
        })
          .then((res)=>{
            this.$emit('relatedCase', res.data)
            this.$emit('next', 2)
          })
      },
      AddLine() {
        let temp = {
          stageName:'',
          regConfKind:'',
          subRegConfKind:'',
          value:''
        }
        this.list.push(temp)
      },
      // DeleteLine() {
      //   this.list.pop()
      // },
      up(para) {
        if (para > 0) {
          let temp = this.list[para];
          Vue.set(this.list, para, this.list[para - 1]);
          Vue.set(this.list, para - 1, temp);
        }
      },
      down(para) {
        if (para < this.list.length - 1) {
          let temp = this.list[para];
          Vue.set(this.list, para, this.list[para + 1]);
          Vue.set(this.list, para + 1, temp);
        }
      },
      deleteword(index) {
        this.list.splice(index, 1)
      }
    },
    mounted() {
      console.log(this.caseId1)
      this.caseId1=this.caseId;
      this.$axios({
        method: 'post',
        url: 'http://223.3.93.238:8080/DisputeWeb/mediator/getKeyWordList',
        data:{
          id:sessionStorage.getItem("id"),
          caseId:this.caseId1,
        }
      })
        .then((res) => {
          console.log(res.data.data)
          this.list = res.data.data.keyWordList;
          this.brief=res.data.data.brief;
          this.$emit('next', 1)
        })
          //
          // this.list = this.KeyWordList;
          // this.brief=this.Brief;
    },
    watch:{
      // KeyWordList:function () {
      //   console.log(this.KeyWordList)
      //   this.list = this.KeyWordList;
      //   this.brief=this.Brief;
      // },
      caseId:function () {
        this.caseId1=this.caseId;
      }
    }

  }
</script>

<style scoped>

  .container{
    width: 100%;
    padding-left: 0px;
    padding-right: 0px;
  }
  .info{
    height:640px;
    background-color:rgba(0,0,0,0.03);
  }
  .tableInfo{
    height:580px;
    overflow: scroll;
    overflow-x:hidden;
  }
  label {
    margin: 8px auto;
    font-size: 20px;
    color: #3a8dbe;
  }
  .up,
  .down,
  .deleteword {
    margin-left: 5px;
    margin-right: 5px;
    width: 40px;
  }
  .ButtonID{
    width: 100%;
    text-align: center;
    margin-top: 20px;
  }

  .ButtonID button {
    width: 80px;
  }
  .confirm {
    margin-top: 20px;
    text-align: center;
  }
  .confirm button {
    width: 80px;
    margin-left:30px;
    margin-right: 30px;
  }
  button {
    font-size: 16px;
    border-radius: 5px;
  }
  #show{
    width: 100%;
    height:640px;
    overflow: scroll;
    overflow-x:hidden;
    padding: 10px 20px;
    border-radius: 5px;
    background-color:rgba(0,0,0,0.03);
    border: none;
  }
 .inputClass{
    background: none;
    border: none;
   width: 100%;
   text-align: center;
  }
</style>



