<template>
  <div id="progress">
    <div class="table-responsive">
      <table class="table table-bordered table-hover">
        <thead class="tablehead">
        <tr>
          <th>案件时间</th>
          <th>案件名称</th>
          <th>案件状态</th>
          <th>调解员</th>
          <th>操作</th>
          <th>评价</th>
          <th>上传资料</th>
          <th>详情</th>
        </tr>
        </thead>
        <tbody v-for="(item,index) in data" class="tablebody">
        <tr>
