                    <button class="btn btn-warning" @click="Repealing('撤销调解',index,item)">撤销调解</button>
                  </span>
              </div>
              <div v-else-if="statuses[item.status]=='调解中'">
                  <span>
                    <button class="btn btn-warning" @click="Repealing('撤销调解',index,item)">撤销调解</button>
                    <button class="btn btn-warning" @click="Repealing('更换调解员',index,item)">更换调解员</button>
                  </span>
