                <!--:value="item" v-if="item!='用户评价'&&item!='撤销申请'">-->
                <!--</option>-->
                <!--</select>-->
              </div>
              <div v-else-if="statuses[item.status]=='调解失败'">
                <span> <button class="btn btn-warning" @click="ReMediation(item)">{{'申请再次调解'}}</button></span>
              </div>
            </template>
          </td>
          <td><button class="btn btn-info" @click="Repealing('用户评价',index,item)">用户评价</button></td>
