        uploadShow:false,
        counter: '',
        MediatorList: [],
        dialogTitle: '',
        IsShowDialog: false,
        IsShowGroup: [
          false, false, false,false
        ],
        operationChosed:'',
        data:[],
        // data: [
        //   {
        //     caseId:1,
        //     date: '2013/1/2',
        //     name: "张三与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
        //     status: 0,
        //   },
        //   {
