        //     date: '2013/1/2',
        //     name: "许译文与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
        //     status: 7,
        //   },
        //   {
        //     caseId:11,
        //     date: '2013/1/2',
        //     name: "许译文与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
        //     status: 8,
        //   },
        //   {
        //     caseId:12,
        //     date: '2013/1/2',
        //     name: "许译文与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
        //     status: 8,
        //   },
        //   {
        //     caseId:13,
        //     date: '2013/1/2',
        //     name: "许译文与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
        //     status: 7,
        //   },
        //   {
        //     caseId:14,
        //     date: '2013/1/2',
        //     name: "许译文与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
        //     status: 8,
        //   },
        // ],
        statuses: ['申请中','等待分配', '调解中','调解失败', '结案中', '已结案', '撤销申请', '撤销调解','立案失败','等待受理','撤销申请中','撤销调解中','申请更换调解员中'],
        statusColors: ['warning', 'warning', 'success','danger', 'success', 'info', 'info', 'info','danger','warning','warning','warning','warning'],
        options: ['撤销申请','撤销调解', '更换调解员','用户评价'],
        // appraisalCenters: ['鉴定中心A', '鉴定中心b', '鉴定中心c', '鉴定中心s', '鉴定中心f', '鉴定中心d',],
        currentPage: 1,
        currentPageSize: 5,
        PageSizes: [2, 5, 10],
        totalPages:1,
      }
    },
    mounted() {
      // if (this.role == '0')
      //   this.ShowUserView = true;
      // else if (this.role === null) {
      // }
      // else
      //   this.ShowUserView = false;
      this.$axios.post('https://139.199.166.125:443/user/getUserCaseList',{id:sessionStorage.getItem("id")})
        .then((res) => {
          this.data = res.data.data
        })
        .catch((error) => {
          console.log(error)
        });

      this.$axios({
        method: 'post',
        url: 'https://139.199.166.125:443/DisputeWeb/manager/getMediatorListWithPage',
        data:{
          // caseId:this.caseId,
          size:this.currentPageSize,
          page:this.currentPage,
        }
      })
        .then((res) => {
          this.MediatorList = res.data.data.mediatorFormList;
          this.totalPages=res.data.data.totalPages
          console.log(this.MediatorList)
        })
    },
    methods: {
      getFile(event) {
        this.file = event.target.files[0];
      },
      upload(para) {
        this.uploadShow = true;
        this.currentCaseId=para.caseId;
        this.currentStatus=para.status;
      },
      submit1() {
        let formData = new FormData();
        if(this.file!=''){
          formData.append('file',this.file);
        }
        formData.append('id',sessionStorage.getItem("id"))
        formData.append('status',this.currentStatus)
        formData.append('caseId',this.currentCaseId)
        formData.append('fileMessage',this.fileMeassage)
        this.$axios({
          method: 'post',
          url: 'https://139.199.166.125:443/user/uploadFile',
          data: formData,
        })
          .then(res=>{
            this.uploadShow = false;
          })

      },
      closeEditDialog1() {
        this.uploadShow = false;
      },
      Repealing(para,index,item) {
        this.currentCaseId=item.caseId;
        this.currentStatus=item.status;
        console.log(this.currentCaseId)
        this.ChangeEvent(index,para,item);
      },
      closeEditDialog() {
        this.IsShowDialog = false;
        this.operationChosed=''
      },
      submit() {
        if(this.dialogTitle=='撤销申请') {
          console.log(this.currentCaseId)
          this.$axios({
            method: 'post',
            url: 'https://139.199.166.125:443/user/caseCancelApply',
            data: {
              id:this.id,
              caseId: this.currentCaseId,
              reason: this.reason,
            }
          })
            .then(res=>{
              if(res.data.code>=0){
                let index=this.data.findIndex((item)=>{
                  return item.caseId==this.currentCaseId
                });
                Vue.set(this.data,index,{
                  caseId:this.currentCaseId,
                  date: this.data[index].date,
                  name: this.data[index].name,
                  currentMedator:this.data[index].currentMedator,
                  status: 10,
                })
              }
            })
        }
        if(this.dialogTitle=='撤销调解') {
          this.$axios({
            method: 'post',
            url: 'https://139.199.166.125:443/user/caseCancelMediation',
            data: {
              id:this.id,
              caseId: this.currentCaseId,
              reason: this.reason,}
          })
            .then(res=> {
              if(res.data.code>=0) {
                let index = this.data.findIndex((item) => {
                  return item.caseId == this.currentCaseId
                })
                Vue.set(this.data, index, {
                  caseId: this.currentCaseId,
                  date: this.data[index].date,
                  name: this.data[index].name,
                  currentMedator:this.data[index].currentMedator,
                  status: 11,
                })
              }
            })
        }
        if(this.dialogTitle=='更换调解员') {
          this.$axios({
            method: 'post',
            url: 'https://139.199.166.125:443/user/changeMediator',
            data: {
              id:this.id,
              caseId: this.currentCaseId,
              mediatorIdList:this.mediatorIdList,
            }
          })
            .then(res=>{
              if(res.data.code>=0) {
                let index = this.data.findIndex((item) => {
                  return item.caseId == this.currentCaseId
                });
                Vue.set(this.data, index, {
                  caseId: this.currentCaseId,
                  date: this.data[index].date,
                  name: this.data[index].name,
                  currentMedator:this.data[index].currentMedator,
                  status: 12,
                })
              }
            })
        }
        if(this.dialogTitle=='用户评价') {
          this.$axios({
            method: 'post',
            url: 'https://139.199.166.125:443/user/addComment',
            data: {
              id:this.id,
              caseId: this.currentCaseId,
              status:this.currentStatus,
              mediatorEvaluation:this.mediatorEvaluation,
              otherEvaluation:this.otherEvaluation,
            }
          })
            .then(res=> {
              if(res.data.code>=0) {
                let index = this.data.findIndex((item) => {
                  return item.caseId == this.currentCaseId
                });
                Vue.set(this.data, index, {
                  caseId: this.currentCaseId,
                  date: this.data[index].date,
                  name: this.data[index].name,
                  currentMedator:this.data[index].currentMedator,
                  status: 4,
                })
              }
            })
        }
        this.closeEditDialog();
      },
      ChangeEvent(index,e,item) {
        this.currentCaseId=item.caseId;
        this.counter=index;
        this.IsShowDialog = true;
        let i = 0;
        this.dialogTitle=e;
        let arrayIndex = this.options.indexOf(e);
        for (; i < this.IsShowGroup.length; i++) {
          if (i == arrayIndex) {
            Vue.set(this.IsShowGroup, i, 'true');
          }
          else {
            this.IsShowGroup[i] = false;
          }
        }
      },

      // ReMediation(caseId){
      //   console.log(caseId);
      //   let r=confirm('是否申请再次调解?');
      //   if(r==true){
      //     let index=this.data.findIndex(item=>{
      //       return item.caseId==caseId
      //     })
      //     Vue.set(this.data,index,{
      //       caseId:caseId,
      //       date: this.data[index].date,
      //       name:  this.data[index].name,
      //       status: 2,
      //       radio:this.data[index].radio
      //     })
      //
      //     this.$axios({
      //       method:'post',
      //       url:'https://139.199.166.125:443/DisputeWeb/user/reMediation',
      //       data:{
      //         id:this.id,
      //         caseId:caseId}
      //     })
      //   }
      // },
      ForCaseDetail(para) {
        let caseState = this.statuses[para.status];
        let caseName = para.name;
        let caseId = para.caseId;
        let route = this.$router.resolve({
          path: '/CaseDetailforUser',
          query: {
            prePath: this.$route.path,
            caseName: caseName,
            caseState: caseState,
            caseId: caseId
          }
        });
        window.open(route.href, '_blank');
      },

      //分页
      PreviousPage(){
        if(this.currentPage>1) {
          this.currentPage = this.currentPage - 1;
          this.currentPageContent();
        }
      },
      NextPage(){
        if(this.currentPage<this.totalPages){
          this.currentPage=this.currentPage+1;
          this.currentPageContent();
        }
      },
      changeStatus(){
        this.currentPage=1;
        this.currentPageContent();
      },
      //显示当前页内容
      currentPageContent(){
        if(this.currentPage<=0)
        {
          alert("页数必须大于0")
          this.currentPage=1;
        }
        this.$axios({
          method: 'post',
          url: 'https://139.199.166.125:443/DisputeWeb/manager/getMediatorListWithPage',
          data:{
            // caseId:this.caseId,
            size:this.currentPageSize,
            page:this.currentPage,
          }
        })
          .then((res) => {
            this.MediatorList = res.data.data.mediatorFormList;
            this.totalPages=res.data.data.totalPages
            console.log(this.MediatorList)
          })
      },
    },
  }
</script>

<style scoped>
  #progress {
    padding: 8px 8px;
  }

  .detail{
    width: 100%;
    border-radius: 5px;
    border-style: ridge;
    background-color:rgba(0,0,0,0.02);
  }
  .table{
    margin-top: 10px;
  }
  .tablehead{
    font-size: 18px;
  }
  .tablebody{
    font-size: 16px;
  }
  .label1{
    font-size: 16px;
    color: #3a8dbe;
  }
  .row{
    margin-bottom: 10px;
  }
</style>



