<template>
  <div class="ListName">
    <label class="label1">调解员列表</label>
    <div class="info">
      <div class="container" style="width: 100%;margin-top:20px">
        <div class="row">
          <div class="col-lg-2 col-md-2 col-sm-2 col-xs-2" v-for="item in MediatorList" style="height: 50px">
            <input type="checkbox" :id="item.mediatorId"  :value="item.mediatorId" v-model="list"><label class="label2" :for="item.mediatorId">{{item.mediatorName}}</label>
          </div>
        </div>
      </div>
    </div>
    <!--分页-->
    <div style="font-size: 14px">
      共{{totalPages}}页
      <select  v-model="currentPageSize" style="margin-left: 30px" @change="changeStatus">
        <option v-for="item in PageSizes" :value="item" :label="item"></option>
      </select>条/页

      <button class="btn btn-info" @click="PreviousPage" style="margin-left: 30px">上一页</button>
      <!--第{{currentPage}}页-->
      <button class="btn btn-info" @click="NextPage"  style="margin-right: 30px">下一页</button>

      前往第
      <!--<input type="number" style="width:50px" v-model="currentPage" @change="currentPageContent" min="1">页-->
      <input type="number" style="width:50px" v-model="currentPage" @keyup.enter="currentPageContent" min="1">页
    </div>

    <div class="bottom">
      <button class="btn btn-primary" @click="PreviousStep">上一步</button>
      <button class="btn btn-primary" @click="change(0)" data-toggle="modal" data-target="#confirm">提交</button>
      <button class="btn btn-primary" @click="change(1)" data-toggle="modal" data-target="#confirm">跳过</button>
    </div>

    <!-- 通知确认框 -->
    <div class="modal fade" id="confirm" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
            <h4 class="modal-title" id="myModalLabel">确认框</h4>
          </div>
          <div class="modal-body" v-if="flag==0">是否提交？</div>
          <div class="modal-body" v-if="flag==1">是否跳过？</div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" @click="chooseMediator" v-if="flag==0">确定</button>
            <button type="button" class="btn btn-primary" @click="skip" v-if="flag==1">确定</button>
            <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
          </div>
        </div>
      </div>
    </div>


  </div>
</template>

<script>
  export default {
    name: "InforOfModerator",
    data() {
      return {
        MediatorList: [],
        MediatorPicked: [],
        list: [],
        caseId:sessionStorage.getItem('caseId'),
        currentPage: 1,
        currentPageSize: 5,
        PageSizes: [2, 5, 10],
        totalPages:1,
        flag:'',
      }
    },
    mounted() {
      this.$axios({
        method: 'post',
        url: 'http://223.3.93.238:8080/DisputeWeb/manager/getMediatorListWithPage',
        data:{
          // caseId:this.caseId,
          size:this.currentPageSize,
          page:this.currentPage,

        }
      })
        .then((res) => {
          this.MediatorList = res.data.data.mediatorFormList;
          this.totalPages=res.data.data.totalPages
          console.log(this.MediatorList)
        })
    },
    methods: {
      change(para){
        this.flag=para;
      },
      PreviousStep(){
        // this.$router.push({path: 'BasicDivideInfo'});
        this.$emit('previous', 4)
      },
      chooseMediator() {
        this.$axios({
          method: 'post',
          url: 'http://223.3.93.238:8080/DisputeWeb/user/postMediatorList',
          data: {
            CaseId:this.caseId,
            MediatorPickedlist: this.list}
        });
        $("#confirm").modal("hide");
        alert('已提交')
        // this.$emit('infoOfMediator', this.list)
        location.reload();
      },
      skip() {
        $("#confirm").modal("hide");
        alert('已提交')
        location.reload();
      },
      //分页
      PreviousPage(){
        if(this.currentPage>1) {
          this.currentPage = this.currentPage - 1;
          this.currentPageContent();
        }
      },
      NextPage(){
        if(this.currentPage<this.totalPages){
          this.currentPage=this.currentPage+1;
          this.currentPageContent();
        }
      },
      changeStatus(){
        this.currentPage=1;
        this.currentPageContent();
      },
      //显示当前页内容
      currentPageContent(){
        if(this.currentPage<=0)
        {
          alert("页数必须大于0")
          this.currentPage=1;
        }
        this.$axios({
          method: 'post',
          url: 'http://223.3.93.238:8080/DisputeWeb/manager/getMediatorListWithPage',
          data:{
            // caseId:this.caseId,
            size:this.currentPageSize,
            page:this.currentPage,
          }
        })
          .then((res) => {
            this.MediatorList = res.data.data.mediatorFormList;
            this.totalPages=res.data.data.totalPages
            console.log(this.MediatorList)
          })
      },
    }
  }
</script>

<style scoped>
  .ListName {
    width: 100%;
  }
  .info{
    height:630px;
    overflow: scroll;
    overflow-x:hidden;
    padding: 10px 20px;
    border-radius: 10px;
    background-color:rgba(0,0,0,0.03);
  }
  radio{
    display: inline;
  }
  .label1 {
    margin: 8px auto;
    font-size: 20px;
    color: #3a8dbe;
  }
  .label2{
    margin: 8px 8px;
    font-size: 18px;
    color: #3a8dbe;
  }

  .bottom{
    margin-top: 20px;
    text-align: center;
  }
  .bottom button{
    margin-left:30px;
    margin-right: 30px;
    width: 80px;
    font-size: 16px
  }

  button{
    border-radius: 5px;
    outline: none;
    border: none;
  }
</style>



