<template>
  <div>
    <div class="info">
      <div class="personInfo">
        <label class="label1">涉事人员信息</label>
        <div class="tableInfo">
          <table class="table1">
            <tr v-for="(item,index) in InvolvedPeople">
              <td>
                <input type="radio"  v-model="item.picked" value="申请人" >
                <label class="label2" >申请人</label>
              </td>
              <td>
                <input type="radio"  v-model="item.picked" value="被申请人">
                <label class="label2">被申请人</label>
              </td>
              <td>
                <input type="radio" :id="index" v-model ="item.picked" value="代理人" @change="handle(index)">
                <label class="label2">代理人</label>
              </td>
              <td>
                <label class="label2">姓名</label>
                <input type="text" v-model="item.name" class="inputClass">
              </td>
              <td>
                <label class="label2">身份证号</label>
                <input type="text" v-model="item.cardID" class="inputClass" @change="getHistory">
              </td>
              <td>
                <label class="label2">手机号码</label>
                <input type="text" v-model="item.phone" class="inputClass">
              </td>
              <td>
                <button class="btn btn-primary btn-sm" @click="DeletePerson(index)">x</button>
              </td>
            </tr>
          </table>
        </div>
        <div class="ButtonID">
          <button class="btn btn-primary" @click="AddPerson()">添加</button>
        </div>
      </div>

      <label class="label1">历史案例</label>
      <div class="show">
        <div class="history">
          <table class="table  table-bordered table-hover" v-if="role=='1'" >
            <thead class="tablehead">
            <tr>
              <td width="100px">案件时间</td>
              <td>案件名称</td>
              <td width="100px">申请人</td>
              <td width="120px">被申请人</td>
              <td >简要情况</td>
              <td>详情</td>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in history" style="border: none">
              <td>{{new Date(item.applyTime).toLocaleDateString()}}</td>
              <td>{{item.caseName}}</td>
              <td>{{item.applyPerson}}</td>
              <td>{{item.hospital}}</td>
              <td>{{item.biefCase}}</td>
              <button class="btn btn-primary" @click="ForCaseDetail(item)">查看详情</button>
            </tr>
            </tbody>
          </table>
        </div>
        <!--分页-->
        <div style="font-size: 15px" v-if="role=='1'">
          共{{totalPages}}页
          <select  v-model="currentPageSize" style="margin-left: 30px" @change="changeStatus">
            <option v-for="item in PageSizes" :value="item" :label="item"></option>
          </select>条/页

          <button class="btn btn-info" @click="PreviousPage" style="margin-left: 30px;font-size: 14px">上一页</button>
          <!--第{{currentPage}}页-->
          <button class="btn btn-info" @click="NextPage"  style="margin-right: 30px;font-size: 14px">下一页</button>

          前往第
          <!--<input type="number" style="width:50px" v-model="currentPage" @change="currentPageContent" min="1">页-->
          <input type="number" style="width:50px;background-color: transparent" v-model="currentPage" @keyup.enter="currentPageContent" min="1">页
        </div>
      </div>
    </div>

    <div class="confirm">
      <button class="btn btn-primary" @click="NextStep">下一步</button>
    </div>
  </div>

</template>

<script>
  import Vue from 'vue'
  export default {
    name: "InvolvedInfo",
    data() {
      return {
        phone:sessionStorage.getItem("phone"),
        role:sessionStorage.getItem("role"),
        id:sessionStorage.getItem("id"),
        caseId:sessionStorage.getItem("CaseId"),
        items: [1, 2, 3],
        ClickNum: 4,
        NumOfSub:0,
        InvolvedPeople: [
          {
            name: "",
            cardID: "",
            phone:"",
            picked: ''
          },
          {
            name: "",
            cardID: "",
            phone:"",
            picked: ''
          },
          {
            name: "",
            cardID: "",
            phone:"",
            picked: ''
          }
        ],
        history:[],
        ResultInvolvedPeole:[],
        currentPage: 1,
        currentPageSize: 5,
        PageSizes: [2, 5, 10],
        totalPages:1,
      }
    },
    methods: {
      handle(para){
        console.log("para"+para);
        for(var i=0;i<this.InvolvedPeople.length;i++){
          if(this.InvolvedPeople[i].picked==="代理人"){
            this.NumOfSub++;
          }
          if(this.NumOfSub>=4){
            alert("代理人至多三人");
            Vue.set(this.InvolvedPeople,para,{
              name: "",
              cardID: "",
              phone:"",
              picked: ""
            });
            break;
          }
        }
        this.NumOfSub=0;
      },
      AddPerson() {
        let temp = {
          name: '',
          cardID: '',
          picked: '',
          phone:'',
        };
        this.InvolvedPeople.push(temp)
      },
      DeletePerson(index) {
        this.InvolvedPeople.splice(index,1)
      },
      NextStep() {
        this.ResultInvolvedPeole=this.InvolvedPeople.filter((item)=>{
          return item.phone!==''
        })
        // this.$router.push({path: 'BasicDivideInfo'});

        // this.$axios({
        //   method: 'post',
        //   url: 'http://223.3.93.238:8080/RegConflict/InvolvedPeopleInfo',
        //   data: {
        //     InvolvedPeople:this.ResultInvolvedPeole,
        //     id:this.id,
        //   }
        // })
        //   .then((res)=>{
        //     this.caseId=res.data.data.CaseId;
        //     // sessionStorage.setItem("caseId",this.caseId)
        //     this.$emit('next', 0)
        //   })
        //   .catch((error) => {
        //     console.log(error)
        //   })
        if(this.ResultInvolvedPeole!=''){
          this.$emit('involvedPeople', this.ResultInvolvedPeole)
          this.$emit('next', 0)
        }
        else{
          alert("请至少填一项涉事人员信息")
        }
      },
      ForCaseDetail(para) {
        let caseName = para.caseName;
        let caseId = para.caseId;
        let route = this.$router.resolve({
          path: '/CaseDetailPage',
          query: {
            prePath: this.$route.path,
            caseName: caseName,
            caseId: caseId
          }
        });
        window.open(route.href, '_blank');
      },
      //分页1
      PreviousPage(){
        if(this.currentPage>1) {
          this.currentPage = this.currentPage - 1;
          this.currentPageContent();
        }
      },
      NextPage(){
        if(this.currentPage<this.totalPages){
          this.currentPage=this.currentPage+1;
          this.currentPageContent();
        }
      },
      changeStatus(){
        this.currentPage=1;
        this.currentPageContent();
      },
      //显示当前页内容
      currentPageContent(){
        if(this.currentPage<=0)
        {
          alert("页数必须大于0")
          this.currentPage=1;
        }
        let temp=this.InvolvedPeople.filter((item)=>{
          return item.phone!==''
        })
        let cardId=[]
        for(let i=0;i<temp.length;i++)
          cardId.push(temp[i].cardID)
        this.$axios({
          method: 'post',
          url: 'http://223.3.93.238:8080/RegConflict/getHistoryCase',
          data: {
            cardId:cardId,
            size:this.currentPageSize,
            page:this.currentPage,
          }
        })
          .then((res)=>{
            this.history=res.data.data.HistoryCaseFormList;
            this.totalPages=res.data.data.totalPages
            this.history.forEach((item)=>{
              item.applicant=item.applicant.join('  ');
            })
            this.history.forEach((item)=>{
              item.respondent=item.respondent.join('  ');
            })
          })
          .catch((error) => {
            console.log(error)
          })
      },
      getHistory(){
          // let temp=this.InvolvedPeople.filter((item)=>{
          //   return item.phone!==''
          // })
          let cardId=[]
          for(let i=0;i<this.InvolvedPeople.length;i++)
          {
            if(this.InvolvedPeople[i].cardID!='')
              cardId.push(this.InvolvedPeople[i].cardID)
          }
          this.$axios({
            method: 'post',
            url: 'http://223.3.93.238:8080/RegConflict/getHistoryCase',
            data: {
              cardId:cardId,
              size:this.currentPageSize,
              page:this.currentPage,
            }
          })
            .then((res)=>{
              this.history=res.data.data.HistoryCaseFormList;
              this.totalPages=res.data.data.totalPages
              this.history.forEach((item)=>{
                item.applicant=item.applicant.join('  ');
              })
              this.history.forEach((item)=>{
                item.respondent=item.respondent.join('  ');
              })
            })
            .catch((error) => {
              console.log(error)
            })
        }
    },


  }
</script>

<style scoped>
  .info {
    width: 100%;
  }
  .tableInfo{
    height:200px;
    overflow: scroll;
    overflow-x:hidden;
    padding: 5px 15px;
    border-radius: 10px;
    background-color:rgba(0,0,0,0.03);
  }
  .show{
    height:300px;
    /*padding: 10px 20px;*/
    border-radius: 10px;
    background-color:rgba(0,0,0,0.03);
  }

  .table1{
    width: 100%;
    border-collapse:   separate;
    border-spacing:   20px;
  }
  .history{
    height:280px;
    /*background-color:rgba(0,0,0,0.05);*/
    margin-bottom: 15px;
    overflow: scroll;
    overflow-x:hidden;
  }

  .label1 {
    margin: 8px auto;
    font-size: 20px;
    color: #3a8dbe;
  }

  .label2{
    font-weight: lighter;
  }
.tablehead{
  font-weight: bold;
  color: #3a8dbe;
}
  .inputClass {
    /*width: 180px;*/
    background: none;
    border-radius: 10px;
  }

  select {
    background-color: transparent;
  }

  .ButtonID{
    width: 100%;
    text-align: center;
    margin-top: 15px;
    /*margin-bottom: 10px;*/
  }

  .ButtonID button {
    width: 80px;
  }
  .confirm {
    width: 100%;
    text-align: center;
    margin-top: 20px;
  }
  button {
    font-size: 16px;
    border-radius: 5px;
  }
</style>



