  import KeyWord from './KeyWord'
  import RelatedCase from './RelatedCase'
  import InfoOfModerator from './InfoOfModerator'
  import ModeratorRegister from './ModeratorRegister'
  import Vue from 'vue'
  export default {
    components: {
      BasicDivideInfo, InvolvedInfo, KeyWord,RelatedCase,InfoOfModerator,ModeratorRegister,
      LoginForm,RegisterForm
    },
    data() {
      return {
        currentView:'InvolvedInfo',
        // date: '',
        // address: '鼓楼区医患纠纷调解委员会',
