        role :sessionStorage.getItem("role"),
        id:sessionStorage.getItem("id"),
        isLogin:sessionStorage.getItem("isLogin"),
        caseId:'',
        flag: [true, false, false,false,false,false],
        path: '',
        Infos: [
          {
            name: '涉事人员信息',
            path: 'InvolvedInfoPage',
          },
          {
            name: '医疗过程描述',
            path: 'BasicDivideInfo'
          },
          {
            name: '纠纷要素预览',
            path: 'KeyWordPage'
          },
          {
            name: '相关案例推荐',
            path: 'RelatedCasePage'
          },
        ],
        brief:'',
        regConflictData:{
          InvolvedPeople:{},
          BasicDivideInfo:{},
          KeyWordList:{},
          RelatedCase:{},
          InfoOfMediator:{},
          ModeratorRegister:{},
        }
      }
    },
    methods:{
      toggle(e) {
        if (e == 0)
          this.currentView = 'InvolvedInfo'
        else if (e == 1)
          this.currentView = 'BasicDivideInfo'
        else if(e==2)
          this.currentView = 'KeyWord'
        else if(e==3)
          this.currentView = 'RelatedCase'
        else if(e==4)
          this.currentView = 'InfoOfModerator'
        else if(e==5 )
          this.currentView = 'ModeratorRegister'

        let i = 0;
        for (; i < this.flag.length; i++) {
          if (i == e) {
            Vue.set(this.flag, e, 'true');
          }
          else {
            this.flag[i] = false;
          }
        }
      },
      nextStep(e){
        this.toggle(e+1)
      },
      previousStep(e){
        this.toggle(e-1)
      },
      getCaseId(para){
        console.log(para)
        this.caseId=para;
        sessionStorage.setItem('caseId',para);
      },
      involvedPeople(para){
        this.regConflictData.InvolvedPeople=para;
        console.log(this.regConflictData.InvolvedPeople)
      },
      // basicDivideInfo(para){
      //   this.regConflictData.BasicDivideInfo=para;
      //   // console.log(this.regConflictData.BasicDivideInfo)
