      //     data: {
      //       // CaseId:this.caseId,
      //       regConflictData: this.regConflictData
      //     }
      //   })
      //     .then(res=>{
      //       alert('已提交，登记已完成'+'\n'+'请及时下载代理人委托书并在用户中心上传')
      //       location.reload()
      //     })
